.container {
  min-height: 100vh;
  min-width: 100vw;
  position: relative;
  z-index: 2;
  background-color: white;

  > :nth-child(2) {
    width: 40px;
    height: 2px;
    background-color: #ef4b6c;
    margin: 2em auto;
  }

  > h1 {
    margin: 75px 0 0 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 1;
    text-align: center;
    color: #333;
    position: relative;
  }


  .link {
    color: #e31b6d;
    text-decoration: none;

    &:hover {
      text-decoration: underline !important;
      color: adjust-color(#e31b6d, $lightness: 20%, $alpha: 0.8);
    }
  }
  .award {
    margin: 1em auto;
    width: 800px;
    border: 1px solid #eaeaea;
    border-radius: 15px;
    box-shadow: 0 10px 40px -10px rgba(0, 64, 128, .1);
    padding-bottom: 20px;
    @media screen and (max-width: 900px) {
      width: 90%;
    }

    > p {
      color: #757c98;
      width: calc(100% - 30px);
      margin: 0 0 0 20px;
    }
    > img {
      width: 100%;
      border-top-right-radius: 15px;
      border-top-left-radius: 15px;
    }
    .textSectionInfo {
      color: slategrey;
      margin: 10px;
    }
  }

  .warImg {
    width: fit-content;
    height: fit-content;
    @media screen and (max-width: 420px) {
      width: 90%
    }
    @media screen and (min-width: 1370px) {
      position: absolute;
      right: 5%;
    }
  }
}


