.Study {
  z-index: 3;
  position: relative;
  min-height: 100vh;
  width: 100vw;

  .heroImg {
    width: fit-content;
    height: fit-content;
    @media screen and (max-width: 500px) {
      width: 90% !important;
    }
    @media screen and (min-width: 500px) {
      width: 200px;
    }
  }

  .heroContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .bar {
    width: 40px;
    height: 2px;
    background-color: #ef4b6c;
    margin: 2em auto;
  }

  .title {
    margin: 75px 0 0 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 1;
    text-align: center;
    color: #333;
    position: relative;
  }

  .HeroInstitutions {
    margin-top: 5%;
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: space-around;
    align-items: center;



    .HeroInstitution {
      background-color: white;
      border-radius: 15px;
      box-shadow: 0 10px 40px -10px rgba(0, 64, 128, .1);
      width: 400px;
      height: 465px;
      display: flex;
      flex-direction: column;
      margin: 1em;
      //position: relative;

      > :nth-child(2) {
        padding: 20px;

        > :nth-child(1), > :nth-child(2) {
          margin: 0;
        }

        > div > p {
          font-size: 16px;
          color: #757c98;
        }
      }

      @media screen and (max-width: 420px) {
        width: 90%;
      }
      @media screen and (max-width: 930px) {
        height: fit-content;
      }
    }

    .imgInstitution {
      width: 100%;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }

    .link {
      color: #e31b6d;
      text-decoration: none;

      &:hover {
        text-decoration: underline !important;
        color: adjust-color(#e31b6d, $lightness: 20%, $alpha: 0.8);
      }
    }
  }
}
.FooterWhiteMask {
  top: 0;
  position: absolute;
  left: 50%;
  min-width: 100%;
  height: 400px;
  max-height: 30%;
  overflow: hidden;
  transform: translateX(-50%);
  z-index: -1;
}
