p, ul {
  margin: 10px 0;
}

.info {
  width: calc(100% - 30px);
  position: absolute;
  top: 15px;
  right: 15px;
  flex-direction: row;
  justify-content: center;
  display: flex;

  > :nth-child(1), :nth-child(2) {
    background-color: white;
    border-width: 1px;
    font-weight: bold;
  }

  @media screen and (max-width: 421px) {
    display: none;
  }
}

.infoSmallScreen {
  display: flex;
  flex-direction: column;

  > :nth-child(1) {
    margin: 8px 0;
  }

  @media screen and (min-width: 420px) {
    display: none;
  }
}

.keywords {
  display: flex;
  flex-direction: row;
  flex-flow: wrap;
  align-items: center;
  margin: 10px 0 0 0;


  > img {
    margin-right: 8px;
    width: 30px;
    height: 30px;
    border: 1px solid rgba(255, 255, 255, 0);
  }

  > img:hover {
    border: 1px solid #68deb6;
    border-radius: 4px;
    background-color: rgba(160, 231, 207, 0.5);
  }

  > div {
    margin: 4px 8px 4px 0;
  }

  > div:hover {
    filter: invert(1);
  }
}

.tasks {
  font-size: 16px;
  padding: 0 0 0 10px;

  li {
    list-style-type: circle;
    margin-bottom: 6px;
    list-style-position: inside;


  }
}

.textSectionInfo {
  color: slategrey;
  margin: 10px 0 0 0;
}

.learnMoreLink, .role, .keywords, .description {
  margin-left: 10px;
}

.description {
  font-size: 16px;
}

.projectName {
  display: flex;
  flex-direction: row;

  > h3 {
    margin: 0;
    font-size: 25px;
    letter-spacing: 1.25px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    line-height: 1.33;
    color: #1b2025;
  }

  @media screen and (max-width: 420px) {
    > h3 {
      margin-top: 20px;
    }
    > img {
      top: -10px;
    }
    > :nth-child(2) {
      left: -15px;
    }
    > :nth-child(3) {
      left: 45px;
    }
  }

}

.projectInformationContainer {
  padding: 20px;
  color: #757c98;
}

.projectImageContainer {
  width: 399px;
  height: 100% !important;
}

.project {
  overflow: hidden;
  > img {
    width: 100%;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
  }

  position: relative;
  margin: 1em auto;
  width: 800px;
  border: 1px solid #eaeaea;
  border-radius: 15px;
  box-shadow: 0 10px 40px -10px rgba(0, 64, 128, .1);

  @media screen and (max-width: 900px) {
    width: 90%;
  }

}


.learnMoreLink {
  display: flex;
  flex-direction: row;
  align-items: center;

  > :first-child {
    margin-right: 0.5em;
  }
  > :nth-child(2) {
    > :nth-child(1) {
      margin: 0 0.5em 0 0;
    }
  }


  p {
    margin: 0;
    font-size: 16px;
  }

}

.link {
  color: #e31b6d;
  text-decoration: none;

  &:hover {
    text-decoration: underline !important;
    color: adjust-color(#e31b6d, $lightness: 20%, $alpha: 0.8);
  }
}

a {
  margin: 0 !important;
}