.container {
  z-index: 100;
  position: absolute;
  left: 20px;
  top: 140px;
  border-radius: 4px;
  overflow: hidden;
  padding-right: 1em;
  background: rgba(52, 58, 64, 0.5);
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.1 );
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  color: white;

  ul>li>ul {
    margin-top: 0;
  }

  li {
    list-style: none;
  }

  a {
    text-decoration: none;

    font-size: 13pt;
    //margin-right: 5px;
    //padding: 10px 12px 0 0;
    color: white;

    &:hover {
      color: #e31b6d;
    }

  }

  @media screen and (max-height: 649px) {
    display: none;
  }
  @media screen and (max-width: 1859px) {
    display: none;
  }
}