.homeSection {
  min-height: 250px !important;
  min-width: 100vw;
}

.particles {
  background-image: linear-gradient(to top right, #3629a7, #343a40);
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.intro {
  font-size: 32pt;
  line-height: 36pt;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.line {
  width: 272px;
  background-color: #BBD336;
  height: 3px;
  padding: 0;
  margin: auto;
}


.heroText {
  z-index: 300;

  .heroFullName {
    color: #ea2c62;
    font-size: 20pt;

    @media screen and (max-width: 400px) {
      font-size: 16pt;
    }
  }

  .heroTitle {
    color: #d37815;
    font-size: 18pt;

    @media screen and (max-width: 400px) {
      font-size: 14pt;
    }
  }

  h1 {
    margin: 10px 0 20px 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    //font-size: 3.5vw;
    line-height: 1;
    text-align: center;
    color: #fff;

  }
}

.section {
  min-height: 100vh;
  width: 100vw;
  position: relative;
  margin: auto;
  z-index: 2;

  .whiteMask {
    bottom: 0;
    position: absolute;
    z-index: 1;
    left: 50%;
    min-width: 100%;
    height: 400px;
    max-height: 40%;
    display: none;
    transform: translateX(-50%);

    @media screen and (max-width: 800px) {
      height: 100px;
    }

    @media screen and (min-width: 500px) and (min-height: 500px) {
      display: block;
    }
  }

  > div {
    //position: absolute;
    //top: 75px;
    //left: 0;
    min-height: 100vh !important;
    z-index: 300;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 450px) {
      justify-content: center;
    }
    @media screen and (max-width: 800px) {
      position: relative;
      flex-direction: column-reverse;
      margin-top: 5em;
    }
  }
}

.highlight {
  color: #e31b6d;
  font-family: "Raleway-semibold";
}

.welcomeTo {
  width: fit-content;
  height: fit-content;
  display: none;
  margin: 1em;

  @media screen and (min-width: 600px) and (min-height: 600px) {
    display: block;
  }
}

.me {

  background-image: url("../../assets/images/extra/Untitled.png");
  background-size: cover;
  width: 240px;
  height: 240px;
  margin: 1em;
  z-index: 300;


}


.contact {
  //width: 100%;
  z-index: 13;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  > :nth-child(1),
  > :nth-child(2),
  > :nth-child(3),
  > :nth-child(4) {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  img {
    width: 50px;
    height: 50px;
    margin: 0 10px;
  }
}

.link {
  color: white;
  font-weight: bold;
  text-decoration: none;

  &:hover {
    cursor: pointer;
    text-decoration: underline !important;
    color: #e31b6d;
  }
}

.language {
  > div, >h3 {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 1em;

    >h3, h4 {
      margin: 0 12px 8px 25px;
      text-shadow: 1px 1px 4px #000000;
    }
  }
}