
.container {
  min-height: 100vh;
  min-width: 100vw;
  background-color: white;
  z-index: 3;
  padding-bottom: 2em;

  > :nth-child(2) {
    width: 40px;
    height: 2px;
    background-color: #ef4b6c;
    margin: 2em auto;
  }

  > h1 {
    margin: 75px 0 0 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 1;
    //letter-spacing: -1px;
    text-align: center;
    color: #333;
    position: relative;
  }
}

.projects {
  display: flex;
  flex-direction: column;
  width: 100%;
}

